<template>
  <div id="invoice-top">
    <van-form @submit="onSubmit"
              v-if="currentInfo">
      <div style="margin:10px 0;">
        <div style="padding:10px 16px;background:#fff;font-weight:bold;">酒店信息</div>
        <van-cell-group>
          <van-field v-model="currentInfo.hotelName"
                     label="酒店名称"
                     placeholder="暂无"
                     readonly />
          <van-field v-model="address"
                     label="酒店地址"
                     placeholder="暂无"
                     readonly />
        </van-cell-group>
      </div>
      <div>
        <van-cell-group>
          <van-field v-model="currentInfo.contacts"
                     label="酒店联系人"
                     placeholder="暂无"
                     readonly />
          <van-field v-model="currentInfo.phone"
                     label="酒店电话"
                     placeholder="暂无"
                     readonly />
          <van-field v-model="realName"
                     label="业务员"
                     placeholder="暂无"
                     readonly />
        </van-cell-group>
      </div>
      <van-button class="commitBtn">提交申请</van-button>
    </van-form>
  </div>
</template>
<script>
import { GetSystemDictDataByType } from '@/api/api_query';
import { GetBusinessHotel } from '@/api/api_hotel';
import { PostBusinessAuditApprovalapply } from '@/api/api_equip';
import { Toast } from 'vant';
export default {
    data() {
        return {
            currentInfo: {},
            realName: '',
            address: '',
            dataForm: {
                applyType: '3',
                hotelId: this.$route.query.hotelId
            }
        };
    },
    created() {
        this.$store.commit('setHeader', '新建申请');
        this.getHotelInfo();
    },
    watch: {},
    methods: {
        getHotelInfo() {
            GetBusinessHotel({ hotelId: this.$route.query.hotelId }).done((res) => {
                this.currentInfo = res.data;
                this.address = this.currentInfo.province + this.currentInfo.city + this.currentInfo.county;
                if (this.currentInfo.businessManager) {
                    this.realName = this.currentInfo.businessManager.realName;
                }
            });
        },
        onSubmit() {
            PostBusinessAuditApprovalapply(this.dataForm).done((res) => {
                Toast.success(res.msg);
                setTimeout(() => {
                    this.$router.go(-1);
                }, 1000);
            });
        },
        getType() {
            GetSystemDictDataByType({ dictType: 'business_payment_method' }).done((res) => {
                this.options = res.data.map((item) => {
                    return {
                        label: item.dictLabel,
                        value: item.dictValue
                    };
                });
                let payments = {};
                for (var i = 0; i < this.options.length; i++) {
                    payments[this.options[i].value] = this.options[i].label;
                }
                this.payments = payments;
            });
        }
    }
};
</script>
<style scoped>
#invoice-top {
    font-size: 0.8rem;
    color: #000;
    text-align: left;
}
.commitBtn {
    width: 100%;
    position: fixed;
    bottom: 0;
    left: 0;
    background: #ff7d41;
    color: #fff;
}
</style>



